/* Sidebar  Footer    */
.yc_challenge_sidebar {
    position: fixed;
    top: 20px;
    left: calc((100vw - 80vw) / 2);
    width: 120px;
    max-height: calc(100vh - 350px); /*    Footer   */
    height: auto;
    display: flex;
    flex-direction: column;
    padding: 20px 10px;
    background-color: #f4f4f4;
    box-sizing: border-box;
    overflow-y: auto;
    z-index: 1000;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
    border-radius: 8px;
    margin-top: 70px;
    margin-bottom: 65px;
}


/* Footer   Sidebar    */
.yc_challenge_sidebar::after {
    content: "";
    display: block;
    height: 50px; /* Footer       */
}


/*   */
.yc_challenge_menu {
    background-color: transparent; /*   */
    flex-grow: 1; /*     */
    margin-bottom: 20px;
    border-radius: 8px;
    /* height: 80vh; */ /* : flex-grow  */
}

/*   */
.yc_challenge_menu ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 100%;
    border-bottom: 1px solid #353535;
    border-top: 1px solid #353535;
}

/*   */
.yc_challenge_menu li {
    padding: 12px 0; /*    */
    margin-bottom: 8px;
    cursor: pointer;
    border-radius: 6px;
    font-size: 14px; /*    */
    color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
}

.yc_challenge_menu li:first-child{
    margin-top: 10px;
}

/*   */
.menu-text {
    margin-top: 5px;
    text-align: center;
    font-size: 12px; /*    */
}
.manage-button{
    margin-top: 5px;
    text-align: center;
    font-size: 12px; /*    */
}

/*   */
.yc_challenge_menu a {
    color: inherit; /*    */
    text-decoration: none; /*   */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

/*    */
.yc_challenge_menu li a svg {
    color: inherit; /*     */
    width: 24px;
    height: 24px;
}

/*       */
.yc_challenge_menu li:not(.disabled):hover,
.yc_challenge_menu li.active:not(.disabled) {
    background-color: #34495e;
    color: white;
    transform: translateY(-1px);
}


/*    */
.yc_challenge_footer-buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: auto; /*   */
}

/*   */
.yc_challenge_report-btn {
    background-color: transparent; /*   */
    color: #c0392b; /*    */
    border: none;
    margin-bottom: 4px;
    border-radius: 8px;
    width: 100%;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.3s ease, transform 0.2s ease;
}

.yc_challenge_report-btn:hover {
    color: #a32415;
    transform: translateY(-2px);
}

/* Exit  */
.yc_challenge_exit-btn {
    background-color: transparent; /*   */
    color: #383E47; /*    */
    border: none;
    border-radius: 8px;
    width: 100%;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.3s ease, transform 0.2s ease;
}

.yc_challenge_exit-btn:hover {
    color: #2c2f33;
    transform: translateY(-2px);
}

/*      */
.disabled-link {
    pointer-events: none; /*   */
    color: gray;
    cursor: default;
}

.disabled-link .menu-text,
.disabled-link svg {
    color: gray;
}

/*  li   */
.yc_challenge_menu li.disabled {
    cursor: default;
}

.yc_challenge_menu li.disabled:hover {
    background-color: transparent; /*    */
    color: gray; /*    */
    transform: none; /*    */
}

/*    */
.yc_challenge_menu li.disabled svg {
    color: gray;
}
