@charset "UTF-8";

/*     */
.wrap .hmk_main-container .hmk_main .hmk_point-history {
    margin-top: 20px;
    /*    */
}

.wrap .hmk_main-container .hmk_main .hmk_point-history h2 {
    margin: 20px;
    font-size: 20px;
    /*    */
    font-weight: bold;
}

/*     */
.wrap .hmk_main-container 
.hmk_main 
.hmk_point-history 
.hmk_point-summary {
    display: flex;
    /*      */
    justify-content: space-between;
    /*    */
    margin-bottom: 20px;
    /*    */

}

.wrap .hmk_main-container 
.hmk_main 
.hmk_point-history 
.hmk_point-summary
.hmk_point-box {
    width: 30%; /*     */
    padding: 20px; /*   */
    background-color: #f4f4f4; /*   */
    border: 1px solid #ddd; /*   */
    border-radius: 8px; /*   */
    display: flex; /*      */
    flex-direction: column; /*    */
    align-items: center; /*    */
    text-align: center; /*    */
}

.wrap .hmk_main-container 
.hmk_main
.hmk_point-history 
.hmk_point-summary
.hmk_point-box 
.hmk_point-value {
    font-size: 24px;
    /*    */
    font-weight: bold;
    /*   */
}

.wrap .hmk_main-container 
.hmk_main
.hmk_point-history
.hmk_point_filterbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    border-radius: 5px;
    box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.2);
}

/*     */
.wrap .hmk_main-container 
.hmk_main
.hmk_point-history
.hmk_point_filterbar
.hmk_point-filter {
    display: flex;
    justify-content: flex-end;
    padding: 15px 15px 0 15px;
    /*   */
    height: 90px;
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_point-filter p {
    font-size: small;
    padding: 12px 0 12px 0;
}

.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-inputs {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 15px 0 15px 0;
    /*   */
}

.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-inputs .hmk_date {
    border-radius: 8px;
    /*   */
}

.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-inputs .hmk_date label {
    font-size: 10px;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-options {
    min-width: 270px;
    margin-right: 10px;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    align-content: center;
    flex-wrap: wrap;
}

.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-options .hmk_date-button {
    height: fit-content;
    margin-right: 5px; /*   */
    padding: 8px 10px; /*   */
    border: none; /*    */
    border-radius: 5px; /*   */
    background-color: #e9ecef; /*   */
    cursor: pointer; /*    */
    transition: background-color 0.3s; /*    */
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-options .hmk_date-button:hover {
    background-color: #f8d7da; /*     */
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-wrapper {
    position: relative;
    /*      */
    display: flex;
    /*      */
    align-items: center;
    /*    */
    margin-bottom: 15px;
    /*    */
}

.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_date-input {
    width: 150px;
    /*    */
    padding: 10px;
    /*   */
    margin-right: 10px;
    /*    */
    border: 1px solid #ccc;
    /*   */
    border-radius: 5px;
    /*   */
    outline: none;
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_calendar-icon {
    cursor: pointer;
    /*    */
    margin-left: 5px;
    /*     */
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_search-button {
    /*  ()*/
    padding: 10px 20px;
    /*   */
    background-color: #007bff;
    /*   */
    color: white;
    /*  */
    border: none;
    /*    */
    border-radius: 5px;
    /*   */
    cursor: pointer;
    /*    */
    transition: background-color 0.3s;
    /*    */
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_point-filter .hmk_search-button:hover {
    background-color: #0056b3;
    /*     */
}

.wrap .hmk_main-container .hmk_main .hmk_point-table {
    width: 100%;
    /*   */
    table-layout: fixed;
    /*   */
    border-collapse: collapse;
    /*    */
    margin-top: 10px;
}

.wrap .hmk_main-container .hmk_main .hmk_point-table thead {
    border-bottom: 1px solid #ededed;
    
}

.wrap .hmk_main-container .hmk_main .hmk_point-table th,
.wrap .hmk_main-container .hmk_main .hmk_point-table td {
    padding: 10px; /*    */
    text-align: center; 
    overflow: hidden; /*    */
    text-overflow: ellipsis; /*   '...'  */
    white-space: nowrap;  /*    */
}

.wrap .hmk_main-container .hmk_main .hmk_point-table tr:nth-child(even) {
    background-color: #f9f9f9; /*    */
}

/*     */
.wrap .hmk_main-container .hmk_toggle-container {
    display: flex; /*   */
    margin-left: 30px;
    margin-bottom: 20px; /*    */
    border-radius: 20px;
    box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.05);
}

/*     */
.wrap .hmk_main-container .hmk_toggle-button {
    min-width: 90px;
    padding: 5px 30px; /*   */
    border: none; /*  */
    background-color: white;
    /*   */
    cursor: pointer;
    /*    */
    transition: background-color 0.3s;
    /*    */
}

/*    () */
.wrap .hmk_main-container .hmk_toggle-button:first-child {
    border-top-left-radius: 20px;
    /*     */
    border-bottom-left-radius: 20px;
    /*     */
}

/*    () */
.wrap .hmk_main-container .hmk_toggle-button:nth-child(2) {
    border-radius: 0;
    /*     */
}

/*    () */
.wrap .hmk_main-container .hmk_toggle-button:last-child {
    border-top-right-radius: 20px;
    /*     */
    border-bottom-right-radius: 20px;
    /*     */
}

/*    */
.wrap .hmk_main-container .hmk_toggle-button.active {
    background-color: #f8d7da;
    /*    */
    border: 1px solid #f8d7da;
}

/*   */
.wrap .hmk_main-container .hmk_toggle-button:hover {
    background-color: #e9ecef;
    /*     */
}

.wrap .hmk_main-container .hmk_toggle-button.active:hover {
    background-color: #f2cccf;
    /*    */
    border: 1px solid #f8d7da;
}

/*          */
.wrap .hmk_main-container .hmk_main .hmk_point-history .hmk_point-table tbody tr td .earned {
    color: #007BFF;
    /*  */
    font-weight: bold;
}

/*          */
.wrap .hmk_main-container .hmk_main .hmk_point-history .hmk_point-table tbody tr td .spent {
    color: #000;
    /*  */
    font-weight: bold;
}