@charset "UTF-8";

/*  */
.dy-link {
    color: black; 
    text-decoration: none; /*   */
}

.dy-link:visited {
    color: black; 
}

.dy-link:hover {
    text-decoration: none; /*   */
}

.wrap {
    margin: 0 auto;

}

/*   */
.dy-loginform {
    width: 600px;
    margin: 0 auto;
    margin-top: 70px;
    text-align: center;

}

/*  */
.dy-loginform .dy-loginTitle {
    font-size: 30px;
    margin-bottom: 30px;
}


/*    */
.dy-loginform .dy-loginform-content {
    width: 400px;
    margin: 0 auto;
    margin-top: 30px;

}


/*    */
.dy-loginform .dy-api-logins {
    margin-bottom: 30px;
}

/*    */
.dy-loginform .dy-kakao-login {
    width: 300px;
    height: 45px;
    /* display: flex; 
    justify-content: center;
    align-items: center; */
    
    /* border: 1px solid black; */
    border-radius: 12px;
    margin-bottom: 10px;
}
.dy-naver-login {
    width: 300px;
    height: 45px;
    margin: 0 auto;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;

    /* background-color: #03c75a; */
    background-color: rgb(3, 199, 90);
    color: white;
}

.dy-loginform .dy-loginform-content .dy-middle {
    margin-bottom: 30px;
}

/*   , input */
.dy-loginform .dy-loginform-content .dy-loginform-login {
    text-align: left;

}

/* input  */
.dy-loginform-content .dy-loginform-login .dy-login-input {
    width: 99%;
    height: 35px;
    border-radius: 15px;
    border: 1px solid black;
    margin-top: 5px;
    margin-bottom: 5px;
}

.dy-loginform-content .dy-loginform-login .dy-error-message {
    color: #e32200;
}

/*   */
.dy-loginform-content .dy-submit-btn {
    width: 100%;
    height: 35px;
    border-radius: 15px;
    margin-top: 30px;

    background-color: #5E91A7;
    color: white;
    border: #497B91;
}

.dy-loginform-content .dy-submit-btn:hover {
    cursor: pointer;
}

/*  */
.dy-loginform-content .dy-password-search {
    margin-top: 30px;
}

/*  */
.dy-loginform-content .dy-to-joinform {
    margin-top: 10px;
    padding-bottom: 40px;
}

