@charset "UTF-8";

/*    */
.wrap .hmk_main-container .hmk_main .hmk_cargobox {
    margin-top: 20px; /*    */
}
.wrap .hmk_main-container .hmk_main .hmk_cargobox h1 {
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
}
.wrap .hmk_main-container .hmk_main .hmk_gift-gallery {
    display: grid; /*    */
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /*   */
    gap: 20px; /*   */
    padding: 0 10px; /*    */
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_gift-card {
    position: relative; /*       */
    /* width: 262px;   */
    height: 322px; /*   */
    overflow: hidden; /*        */
    /*border: 1px solid #ddd; /*    */
    border-radius: 8px; /*    */
    /* background-color: #fff; /*   */
    /* box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /*    */
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame {
    width: 100%; /*     */
    height: 260px; 
    /* background-color: #f4f4f4; /*   */
    /* border: 1px solid #ddd; /*   */
    /* border-radius: 8px; /*    */
    display: flex; /*    */
    justify-content: center; /*    */
    align-items: center; /*    */
    overflow: hidden; /*      */
}

.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame {

    border-radius: 12px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}


/*    */
.wrap .hmk_main-container .hmk_main .hmk_gift-image {
    width: 100%; /*     */
    height: 100%; /*     */
    object-fit: contain; /*      */
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_gift-name-button {
    position: absolute; /*    */
    bottom: 10px; /*   */
    left: 50%; /*   */
    transform: translateX(-50%); /*    */
    padding: 8px 12px; /*   */
    background-color: #E0E0E0; /*   */
    color: rgb(27, 23, 23); /*   */
    border: none; /*    */
    border-radius: 5px; /*   */
    cursor: pointer; /*   */
    transition: background-color 0.3s; /*    */
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_gift-name-button:hover {
    background-color: #cccccc; /*     */
}

/*    */
.hmk_cargo_detail-modal {
    position: fixed; /*   */
    top: 0;
    left: 0;
    width: 100%; /*   */
    height: 100%; /*   */
    background-color: rgba(0, 0, 0, 0.5); /*    */
    display: flex; /* Flexbox  */
    justify-content: center; /*    */
    align-items: center; /*    */
    z-index: 1200; /*     */
}

/*     */
.hmk_cargo_detail-modal-content {
    background-color: #ffffff; /*   */
    padding: 20px; /*   */
    border-radius: 8px; /*   */
    width: 90%; /*  */
    max-width: 300px; /*   */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3); /*   */
    position: relative; /*   */
    text-align: center; /*    */
}

/*       */
.hmk_cargo_detail-content {
    margin-bottom: 15px; /*   */
    font-size: 1.2em; /*   */
    color: #333333; /*   */
}

/*     */
.hmk_cargo_detail-image {
    width: 100%; /*   */
    height: auto; /*   */
    margin-bottom: 15px; /*   */
    border-radius: 4px; /*    */
    object-fit: scale-down; /*    */
}

/*      */
.hmk_cargo_detail-actions {
    display: flex; /* Flexbox  */
    justify-content: center; /*    */
    gap: 10px; /*    */
}

/*     */
.hmk_cargo_btnmodal {
    padding: 10px 20px; /*   */
    background-color: #3a7afe; /*   */
    color: #ffffff; /*    */
    border: none; /*   */
    border-radius: 4px; /*   */
    cursor: pointer; /*     */
    font-size: 1em; /*   */
    transition: background-color 0.3s ease; /*     */
}

.hmk_cargo_btnmodal:hover {
    background-color: #3569c1; /*     */
}

/*      */
.hmk_close-modal {
    /*      */
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
}

.hmk_cargobox h1 {
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
}

/*    ( giftcard.css ) */
.hmk_gift-gallery {
    display: grid; /*    */
    grid-template-columns: repeat(4, 1fr); /*   4   */
    gap: 20px; /*   */
}
